<template>
  <div class="hello">
    <div>chilc component:about</div>
    <h1>{{ msg }}</h1>
    
    <div>
      <button @click="sendMsg()">test emit event, call handler from parent component</button>
    </div>
  </div>

  
</template>

<script>
export default {
  name: "HelloWorld",
  props: {
    msg: String
  },

   methods:{
      sendMsg(){
        this.$emit('child-msg','msg from child component: HelloWorld ,emit event');
      }
    }
};
</script>

